:root {
    --dark-blue: rgba(74, 36, 207, 1);
    --black-blue: rgba(48, 59, 91, 1);
    --light-white: rgba(254, 251, 255,1);
    --light-gray: rgb(163, 162, 167);
    --white: white;
    --green: rgba(51, 176, 144, 1);
    --light-orange: rgba(241, 197, 100,1);
    

    --blue: blue;
    --light-blue: rgb(203, 203, 240);
    
    --danger:rgba(255,0,0,0.9);
    --light-danger:rgb(248, 225, 225);
    
    --orange:orange;
    --light-orange:rgb(245, 226, 190);
    
    --green:green;
    --light-green:rgb(183, 247, 183);
    
}

.nb-full-width {
    width: 44%;
    height: auto;
    display: flex;
    /* align-items: center; */
    margin: 100px auto;
    border-radius: 20px;
    background-color: var(--white);
}
.nb-item {
    display: flex;
    align-items: center;
    flex-direction: column;
    width:50%;
    padding: 15px;
}
.nb-item .inner-item {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 10px 10px 20px 10px;
    background-color: var(--dark-blue);
}
.nb-item .bold {
    font-size: 72px;
    color: var(--white);
}
.nb-item h3 {
    color: var(--white);
}
.nb-item p.bottomp {
    margin: 10px 0px 10px 0px;
    padding: 10px;
    text-align: center;
    font-size: 16px;
    font-family: 'Poppins';
    font-weight: 600;
}
.nb-item p {
    color: var(--light-gray);
}
.blue {
    background-color: var(--blue);
    border-radius: 20px;
}
.light-white {
    color: var(--light-white);
}
.head-name {
    margin: 15px 0px;
    font-size: 24px;
    font-family: 'Poppins';
}
.black-blue {
    color: var(--black-blue);
}
.bottom-name {
    font-size: 28px;
}



.nb-item-second {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 30px;
    /* align-items: flex-start; */
    width: 50%;
}

.second-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 8px;
    padding: 10px;
}
.second-item h4 {
    font-size: 18px;
    font-family: 'Poppins';
}

.second-item .second-h4 {
    color: var(--light-gray);
}

.second-item span.bold {
    color: var(--black-blue);
    padding: 0px 5px;
}

.inner {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
}

.inner .icon {
    line-height: 16px;
    padding-right: 10px;
}

.padd-lx-5 {
    padding-left: 5px;
}


.light-danger {
    background-color: var(--light-danger);
    color: var(--danger);
}

.light-orange {
    background-color: var(--light-orange);
    color: var(--orange);
}

.light-green {
    background-color: var(--light-green);
    color: var(--green);
}

.light-blue {
    background-color: var(--light-blue);
    color: var(--blue);
}

.summary {
    font-size: 24px;
    font-family: 'Poppins';
    color: var(--black-blue);
}

.nb-btn {
    border: none;
    outline: none;
    border-radius: 50px;
    background-color: var(--black-blue);
    color: var(--light-white);
    padding: 15px;
    width: 100%;
    font-size: 18px;
}


@media screen and (max-width:1068px) {
    .nb-full-width {
        width: 50%;
    }
}
@media screen and (max-width:868px) {
    .nb-full-width {
        flex-direction: column;
        width: 55%;
    }
    .nb-item, .nb-item-second {
        width: 100%;
    }
    .second-item {
        margin: 10px 0px;
    }
    .nb-btn {
        margin-top: 20px;
    }
}

@media screen and (max-width:600px) {
    .nb-full-width {
        width: 55%;
    }
}

@media screen and (max-width:420px) {
    .nb-full-width {
        width: 90%;
    }
}

@media screen and (max-width:320px) {
    .nb-full-width {
        width: 100%;
    }
}